			margin-bottom: 0px;
	
		}
		.demo-uni-col {
				height: 36px;
				border-radius: 0px;
			}
		
			.dark_deep {
				background-color: #99a9bf;
			}
		
			.dark {
				background-color: #d3dce6;
			}
		
			.light {
				background-color: #e5e9f2;
			}
		
			.tagButton {
				width: 100%;
				height: 36px;
				display: flex;
				/* justify-content: center;
				align-items: center; */
				border: 2px solid #000;
				cursor: pointer;
				font-weight: bold;
				font-size: 14px;
			  }
			
